<template>
    <vh-modal
        :isOpen.sync="$parent.$data.saveModal"
        :isCenter="true">

        <vh-modal-body>
            <div class="vh-margin-top vh-text-center">
                <h1 class="vh-text-title">
                    💾
                </h1>
                <h2 class="vh-modal-title">
                    Do you want to save the game?
                </h2>

                <form class="vh-form-stacked vh-margin-top"
                    @submit.prevent="submit()">

                    <fieldset class="vh-fieldset">

                        <div class="vh-margin">
                            <label class="vh-form-label vh-text-bold">
                                Name of the game
                            </label>
                            <input
                                type="text"
                                class="axs-input"
                                required="true"
                                autofocus="true"
                                placeholder="ex. AAP ENEMY"
                                    v-model="form.name">

                            <small class="vh-text-danger vh-text-bold"
                                v-if="hasError('name')">
                                👎🏻 Please fill up this required field.
                            </small>
                        </div>

                        <div class="vh-margin-medium-top">
                            <label class="vh-form-label vh-text-bold">
                                Do you win or not?
                            </label>
                            <label for="result-win"
                                style="font-size: 24px;"
                                class="vh-margin-right">
                                <input class="vh-checkbox" id="result-win" type="radio"
                                    :value="'WIN'"
                                    v-model="form.result">
                                <span class="vh-margin-small-left">
                                    WIN
                                </span>
                            </label>
                            <label for="result-lose"
                                style="font-size: 24px;">
                                <input class="vh-checkbox" id="result-lose" type="radio"
                                    :value="'LOSE'"
                                    v-model="form.result">
                                <span class="vh-margin-small-left">
                                    LOSE
                                </span>
                            </label>
                        </div>
                    </fieldset>
                </form>

                <div class="vh-margin-medium-top vh-visible@m">
                    <vh-button
                        type="button"
                        class="vh-margin-small-right vh-margin-xsmall-top"
                        :size="'large'"
                        @click="closeModal()">
                        Cancel
                    </vh-button>
                    <vh-button
                        class="vh-margin-xsmall-top"
                        type="button"
                        :size="'large'"
                        @click="submit()"
                        :color="'primary'">
                        Save
                    </vh-button>
                </div>

                <div class="vh-margin-medium-top vh-hidden@m">
                    <vh-button
                        class="vh-margin-xsmall-top"
                        type="button"
                        :size="'large'"
                        @click="submit()"
                        :color="'primary'">
                        Save
                    </vh-button>
                    <vh-button
                        type="button"
                        class="vh-margin-small-right vh-margin-xsmall-top"
                        :size="'large'"
                        @click="closeModal()">
                        Cancel
                    </vh-button>
                    
                </div>
            </div>
        </vh-modal-body>
    </vh-modal>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

import { Form, Toast, Dater } from '@/utils';

export default
{
    data()
    {
        return {
            errors: {},

            form:
            {
                name: 'AAP ENEMY',
                result: 'WIN',
                created: '',
                rounds: [],
            },
        };
    },

    computed:
    {
        ...mapGetters({
            games: 'auth/games',
        }),
    },

    methods:
    {
        ...mapActions({
            save: 'auth/saveGame',
        }),
        
        async submit()
        {
            this.errors = {};

            if (!this.form.name) this.form.name = `Game ${this.games.length+1}`;

            this.form.created = `${Dater.rawToday()} ${Dater.rawTime()}`;

            this.form.rounds = this.$parent.$data.rounds;
            
            this.save(this.form);

            Toast.success({
                message: '🤟 Game has been saved.'
            });

            this.closeModal();

            this.$router.push('/dashboard');
        },

        hasError(name = null)
        {
            return Form.hasError(this.errors, name);
        },

        closeModal()
        {
            this.$parent.toggleModal('save', false);
        },
    },
};
</script>



