<template>
    <div class="vh-animate vh-animate-fade-in">
        <h1 class="axs-title vh-text-center vh-margin-remove">
            ENEMY'S GAME
        </h1>
        
        <form class="vh-form-stacked"
            @submit.prevent="nextRound()">
            <div class="vh-margin-top vh-text-center">

                <h1 class="vh-text-title vh-margin-remove"
                    data-tooltip="Enemy's Energy"
                    v-if="currentEnergy">
                    <span v-for="index in currentEnergy"
                        :key="index">
                        ⚡️
                    </span>
                </h1>
                <h1 class="vh-text-title vh-margin-remove"
                    data-tooltip="No Energy"
                    v-else>
                    🌪
                </h1>

                <h4 class="vh-text-bold vh-margin-remove">
                    ROUND {{currentRound.roundNumber}}
                </h4>

                <div class="vh-margin-top">
                    <vh-grid :gutter="'small'">
                        <div class="vh-width-1-1@s vh-width-1-5@m vh-width-1-5@l"></div>
                        <div class="vh-width-1-1@s vh-width-3-5@m vh-width-3-5@l">

                            <vh-grid :gutter="'small'"
                                class="vh-flex-middle">
                                <div class="vh-width-expand">
                                    <label class="vh-form-label vh-text-bold">
                                        USED ENERGY
                                    </label>
                                    <input
                                        type="number"
                                        class="axs-input"
                                        placeholder="ex. 2"
                                            v-model="currentRound.usedEnergy">
                                </div>
                                <div class="vh-text-right vh-margin-small-top">
                                    <vh-button
                                        type="button"
                                        :size="'medium'"
                                        :color="'default'"
                                        :outline="true"
                                        :disabled="!currentRound.usedEnergy"
                                        @click="currentRound.usedEnergy--">
                                        ➖ 
                                    </vh-button>


                                    <vh-button
                                        type="button"
                                        :size="'medium'"
                                        :color="'primary'"
                                        :outline="true"
                                        @click="currentRound.usedEnergy++">
                                        ➕
                                    </vh-button>
                                </div>
                            </vh-grid>
                        </div>
                        <div class="vh-width-1-1@s vh-width-1-5@m vh-width-1-5@l"></div>
                    </vh-grid>

                    <vh-grid>
                        <div class="vh-width-1-1@s vh-width-1-5@m vh-width-1-5@l"></div>
                        <div class="vh-width-1-1@s vh-width-3-5@m vh-width-3-5@l">
                            <vh-grid :gutter="'small'"
                                class="vh-flex-middle">
                                <div class="vh-width-expand">
                                    <label class="vh-form-label vh-text-bold">
                                        GAINED ENERGY
                                    </label>
                                    <input
                                        type="number"
                                        class="axs-input"
                                        placeholder="ex. 2"
                                            v-model="currentRound.gainedEnergy">
                                </div>
                                <div class="vh-text-right vh-margin-small-top">
                                    <vh-button
                                        type="button"
                                        :size="'medium'"
                                        :color="'default'"
                                        :outline="true"
                                        :disabled="!currentRound.gainedEnergy"
                                        @click="currentRound.gainedEnergy--">
                                        ➖ 
                                    </vh-button>


                                    <vh-button
                                        type="button"
                                        :size="'medium'"
                                        :color="'primary'"
                                        :outline="true"
                                        @click="currentRound.gainedEnergy++">
                                        ➕
                                    </vh-button>
                                </div>
                            </vh-grid>
                            
                        </div>
                        <div class="vh-width-1-1@s vh-width-1-5@m vh-width-1-5@l"></div>
                    </vh-grid>

                    <vh-grid>
                        <div class="vh-width-1-1@s vh-width-1-5@m vh-width-1-5@l"></div>
                        <div class="vh-width-1-1@s vh-width-3-5@m vh-width-3-5@l">
                            <vh-grid :gutter="'small'"
                                class="vh-flex-middle">
                                <div class="vh-width-expand">
                                    <label class="vh-form-label vh-text-bold">
                                        STOLEN ENERGY
                                    </label>
                                    <input
                                        type="number"
                                        class="axs-input"
                                        placeholder="ex. 2"
                                            v-model="currentRound.stolenEnergy">
                                </div>
                                <div class="vh-text-right vh-margin-small-top">
                                    <vh-button
                                        type="button"
                                        :size="'medium'"
                                        :color="'default'"
                                        :outline="true"
                                        :disabled="!currentRound.stolenEnergy"
                                        @click="currentRound.stolenEnergy--">
                                        ➖ 
                                    </vh-button>


                                    <vh-button
                                        type="button"
                                        :size="'medium'"
                                        :color="'primary'"
                                        :outline="true"
                                        @click="currentRound.stolenEnergy++">
                                        ➕
                                    </vh-button>
                                </div>
                            </vh-grid>
                        </div>
                        <div class="vh-width-1-1@s vh-width-1-5@m vh-width-1-5@l"></div>
                    </vh-grid>


                    <div class="vh-margin-medium-top">
                        <div class="vh-visible@m">
                            <vh-button
                                type="button"
                                v-show="currentRound.round!=1"
                                class="vh-margin-xsmall-top"
                                :size="'large'"
                                :color="'default'"
                                @click="cancelRound()">
                                👈 CANCEL ROUND
                            </vh-button>
                            <vh-button
                                type="submit"
                                class="vh-margin-xsmall-top"
                                :size="'large'"
                                :color="'default'">
                                💪 NEXT ROUND
                            </vh-button>
                        </div>
                        <div class="vh-hidden@m">
                            <vh-button
                                type="submit"
                                class="vh-margin-xsmall-top"
                                :size="'large'"
                                :color="'default'">
                                💪 NEXT ROUND
                            </vh-button>

                            <vh-button
                                type="button"
                                v-show="currentRound.round!=1"
                                class="vh-margin-xsmall-top"
                                :size="'large'"
                                :color="'default'"
                                @click="cancelRound()">
                                👈 CANCEL ROUND
                            </vh-button>
                        </div>

                        <hr class="vh-margin-top">

                        <div class="vh-margin-top">
                            <vh-button
                                type="button"
                                class="vh-margin-xsmall-top"
                                :size="'medium'"
                                :color="'default'"
                                @click="toggleModal('save', true)">
                                💾 SAVE GAME
                            </vh-button>
                            <vh-button
                                type="button"
                                class="vh-margin-xsmall-top"
                                :size="'medium'"
                                :color="'default'"
                                @click="toggleModal('reset', true)">
                                👾 RESET GAME
                            </vh-button>

                            <div class="vh-margin-medium-top">
                                <router-link
                                    class="vh-link-reset vh-text-primary"
                                    :to="`/dashboard`">
                                    BACK TO DASHBOARD
                                </router-link>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        </form>

        <reset-modal />

        <save-modal />

    </div>
</template>

<script>
import { Toast } from '@/utils';

import ResetModal from './components/modals/reset';
import SaveModal from './components/modals/save';

export default
{
    name: 'InGame',

    components:
    {
        ResetModal,
        SaveModal,
    },

    data()
    {
        return {
            resetModal: false,
            saveModal: false,

            rounds: [],
        };
    },

    mounted()
    {
        this.initialize();
    },

    computed:
    {
        currentEnergy()
        {
            let energy = 0;

            this.rounds.forEach((row, index) => {
                let roundEnergy = !index ? 3 : 2;
                let less = parseInt(row.usedEnergy) + parseInt(row.stolenEnergy);
                let more = parseInt(roundEnergy) + parseInt(row.gainedEnergy);

                energy = (energy - less) + more;

                if (energy > 10) energy = 10;
            });

            return energy;
        },

        currentRound()
        {
            return this.rounds.length
                ? this.rounds[this.rounds.length-1]
                : { roundNumber: 1 };
        },
    },

    methods:
    {
        initialize()
        {
            this.rounds.push({
                roundNumber: 1,
                roundEnergy: 3,
                usedEnergy: 0,
                gainedEnergy: 0,
                stolenEnergy: 0,
            });
        },

        nextRound()
        {
            if (this.currentEnergy >= 0)
            {
                this.rounds.push({
                    roundNumber: this.rounds[this.rounds.length-1].roundNumber + 1,
                    roundEnergy: 2,
                    usedEnergy: 0,
                    gainedEnergy: 0,
                    stolenEnergy: 0,
                });
            }
            else
            {
                Toast.error({
                    message: '😡 Over energy computation. Please double check.'
                });
            }
        },

        cancelRound()
        {
            if(this.rounds.length!=1) this.rounds.splice(this.rounds.length-1, 1);
        },

        toggleModal(name, bool)
        {
            this[`${name}Modal`] = bool;
        },
    },
};
</script>



