<template>
    <vh-modal
        :isOpen.sync="$parent.$data.resetModal"
        :isCenter="true">

        <vh-modal-body>
            <div class="vh-margin-top vh-text-center">
                <h1 class="vh-text-title">
                    👾
                </h1>
                <h2 class="vh-modal-title">
                    Are you sure you want to reset the game?
                </h2>

                <div class="vh-margin-top vh-visible@m">
                    <vh-button
                        type="button"
                        class="vh-margin-small-right vh-margin-xsmall-top"
                        :size="'large'"
                        @click="closeModal()">
                        Cancel
                    </vh-button>
                    <vh-button
                        class="vh-margin-xsmall-top"
                        type="button"
                        :size="'large'"
                        @click="submit()"
                        :color="'primary'">
                        Yes
                    </vh-button>
                </div>

                <div class="vh-margin-top vh-hidden@m">
                    <vh-button
                        class="vh-margin-xsmall-top"
                        type="button"
                        :size="'large'"
                        @click="submit()"
                        :color="'primary'">
                        Yes
                    </vh-button>
                    <vh-button
                        type="button"
                        class="vh-margin-small-right vh-margin-xsmall-top"
                        :size="'large'"
                        @click="closeModal()">
                        Cancel
                    </vh-button>
                </div>
            </div>
        </vh-modal-body>
    </vh-modal>
</template>

<script>

import { Form, Toast } from '@/utils';

export default
{
    data()
    {
        return {
            errors: {},
        };
    },

    methods:
    {
        async submit()
        {
            this.errors = {};
            
            this.$parent.$data.rounds = [];

            this.$parent.initialize();

            Toast.success({
                message: '🤟 Game has been reset.'
            });

            this.closeModal();
        },

        hasError(name = null)
        {
            return Form.hasError(this.errors, name);
        },

        closeModal()
        {
            this.$parent.toggleModal('reset', false);
        },
    },
};
</script>



